import React, {useState, useEffect} from "react"
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import axios from "axios";

function EditExercises(props) {
    
    const [exercise, setExercise] = useState({
        username: '',
        description: '',
        duration: 0,
        date: new Date(),
        users: [] // For array of users
    })

    useEffect(() => {
        axios.get("/exercises/" + props.match.params.id)
            .then(res => {
                setExercise({
                    username: res.data.username,
                    description: res.data.description,
                    duration: res.data.duration,
                    date: new Date(res.data.date),
                })
            })
            .catch(err => console.log(err))
    }, []); 
    // NOTE:By passing an empty array as second argument triggers 
    // the callback in useEffect only after the initial render thus
    // replicating ` componentDidMount` lifecycle behaviour

    function onChangeExercise(event) {
        const {name, value} = event.target
        setExercise({
            ...exercise,
            [name]: value
        })
    }

    function onChangeDate(date) {
        setExercise({
            ...exercise,
            date: date
        })
    }

    function onSubmit(e) {
        e.preventDefault();

        const newExercise = {
            username: exercise.username,
            description: exercise.description,
            duration: exercise.duration,
            date: exercise.date,
        }
        
        // HTTP request to the server with axios, can also use the fetch() method
        axios.put("/exercises/" + props.match.params.id, newExercise)
            .then(res => console.log(res.data))

        window.location = "/";
    }


    return (
        <div>
            <h3>Edit exercise log</h3>
            <form onSubmit={onSubmit}>
                <div className="form-group">
                    <label>Username: </label>
                    <select required
                        className="form-control"
                        name="username"
                        value={exercise.username}
                        onChange={onChangeExercise}
                    >
                    <option>{exercise.username}</option>
                    </select>
                </div>
                <div className="form-group">
                <label>Description: </label>
                <input
                type="text"
                className="form-control"
                name="description"
                value={exercise.description}
                onChange={onChangeExercise}
                />
                </div>
                <div className="form-group">
                <label>Duration (in Minutes): </label>
                <input
                type="text"
                className="form-control"
                name="duration"
                value={exercise.duration}
                onChange={onChangeExercise}
                />
                </div>
                <div className="form-group">
                <label>Date: </label>
                    <div>
                        <DatePicker
                        selected={exercise.date}
                        onChange={onChangeDate}
                        />
                    </div>
                </div>
                <div className="form-group">
                    <input type="submit" value="Edit Exercise Log" ClassName="btn btn-primary" />
                </div>
            </form>
        </div>
    )
}

export default EditExercises;