import React, { useState, useEffect } from "react"
import Exercise from "./exerciseTable.component"
import axios from "axios"

function ExercisesList() {
    const [exercisesList, setExercisesList] = useState({ exercises: [] })

    useEffect(() => {
        axios.get("/exercises/")
            .then(res => {
                setExercisesList({ exercises: res.data })
            })
            .catch(err => console.log(err))
    }, [])

    function deleteExercise(id) {
        axios.delete("/exercises/" + id)
            .then(res => console.log(res.data))
        setExercisesList({
            exercises: exercisesList.exercises.filter(exercise => exercise._id !== id)
        })
    }

    function loadExercisesList() {
        return (
            exercisesList.exercises.map(exercise => 
                <Exercise 
                key={exercise._id}
                exercise={exercise}
                deleteExercise={deleteExercise}
            />
        ))
    }

    return (
        <div>
            <h3>Logged Exercises</h3>
            <table className="table">
                <thead className="thead-light">
                    <tr>
                        <th>Username</th>
                        <th>Description</th>
                        <th>Duration</th>
                        <th>Date</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {loadExercisesList()}
                </tbody>
            </table>
        </div>
    )
}

export default ExercisesList;